<template>
    <div page-index>
        <div v-shadow="{ radius: 8 }">
            <p>
                插件-过滤器(字符转大写):
                <input
                    type="text"
                    v-model="value1" />{{ value1 | toUpper }}
            </p>
            <p>
                插件-混入:
                <button
                    type="button"
                    @click="fun">
                    调用插件中的方法
                </button>
            </p>
        </div>
        <div v-card>
            <p>关于插件的使用</p>
            <div>
                <pre>
通常用于功能增强或扩展功能
定义:
  plugin.js->
  export default {
    // 该函数必须存在
    install() {
        console.log('插件已启用')
    }
  }
支持接收参数
默认参数:
  install(Vue) {
    // 该参数的值为Vue的构造函数
    console.log(Vue)
  }
手动传入的参数:
  install(Vue, ...args) {
    ...
  }
  根据这个特性, 可以传入一个Object然后再做下一步处理.
                </pre>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Plugin',
    data() {
        return {
            value1: 'naihe',
        };
    },
};
</script>

<style scoped></style>
